import { fetchEndpoint } from '../server/network'

// 
export const cardpagePreview = name =>
	fetchEndpoint('/v2/ecapi.cardpage.preview', 'POST', {
		name: name // key
	})

// 
export const cardpageGet = name =>
	fetchEndpoint('/v2/ecapi.cardpage.get', 'POST', {
		name: name // key
	})



